.clock {
  height: 70px;
  width: 80px;
  background: #f3f3f3 url("ios_clock.7c63ae86.svg") center / 88% no-repeat;
  border-radius: 50%;
  padding-bottom: 1%;
  transition: all .5s ease-in-out;
  position: fixed;
  top: 14%;
  left: 38%;
}

.clock.simple:after {
  content: "";
  width: 5%;
  height: 5%;
  z-index: 10;
  background: #000;
  border-radius: 50%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.minutes-container, .hours-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.hours {
  height: 20%;
  transform-origin: 50% 100%;
  width: 2.5%;
  background: #000;
  position: absolute;
  top: 30%;
  left: 48.75%;
}

.minutes {
  height: 40%;
  transform-origin: 50% 100%;
  width: 2%;
  background: #000;
  position: absolute;
  top: 10%;
  left: 49%;
}

@keyframes rotate {
  100% {
    transform: rotateZ(360deg);
  }
}

.hours-container {
  animation: rotate 12s linear infinite paused;
}

.animateDescriptor {
  animation: fadeinout 1s;
}

